.label {
  max-width: 400px;
  display: flex;
  margin: 0 auto 40px;
  flex-direction: column;
  font-size: 20px;
  color: var(--primary-color);
}

.input {
  margin-top: 10px;
  font: inherit;
  font-size: 16px;
  margin-bottom: 15px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid var(--primary-color);
  outline: none;
  padding-block: 5px;
  transition: border-color 250ms;
  color: inherit;
}

.input:focus {
  border-color: var(--accent-color);
}
